import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import useAdminContexts from "../Context/AdminContext";
import { fetchVideoss } from "../ReduxProvider/CounterSlice";
import Loader from "./partials/Loader";

const VideoComponent = () => {
  // const formImage = useRef(null)
  const [imgs, setImgs] = useState({});
  const [disable, setDisable] = useState(false);
  const { addVideos, fetchAllVideos, deleteVideos, loader } =
    useAdminContexts();
  const fetchVideos = useSelector(fetchVideoss);

  const add = (e) => {
    setImgs(e.target.files);
  };

  const formData = async (e) => {
    e.preventDefault();
    if (imgs) {
      addVideos(imgs, setDisable);
    } else {
      alert("Please Upload Video For Submit");
      setDisable(false);
    }
  };

  useEffect(() => {
    fetchAllVideos();
    // eslint-disable-next-line
  }, []);

  console.log(imgs);
  return (
    <>
      {loader ? (
        <>
          <Loader />
        </>
      ) : (
        <div className="content">
        <div className="container-fluid">
          <Baner className="row">
            {fetchVideos.map((x, index) => {
              return (
                <div className="col-4" key={index}>
                  <div className="card">
                    <Image>
                      <video
                        controls
                        src={x.AdminVideo}
                        type="video/mp4"
                        alt="Your browser does not support the video tag."
                      />
                      {/* <source src={x.AdminVideo} type="video/mp4" /> */}
                      {/* Your browser does not support the video tag.
                      </video> */}
                      <I>
                        <i
                          className="fa fa-trash"
                          onClick={(e) => {
                            deleteVideos(x._id, x.AdminVideo);
                          }}
                        />
                      </I>
                    </Image>
                  </div>
                </div>
              );
            })}
            <div className="col-sm-4">
              <div className="card">
                <AddImage className="">
                  <label htmlFor="files">Select files:</label>
                  <input
                    type="file"
                    accept=".mp4,"
                    id="video"
                    name="video"
                    onChange={add}
                    multiple
                  />
                  <button
                    type="button"
                    disable={disable}
                    onClick={(e) => {
                      formData(e);
                      setDisable(true);
                    }}
                  >
                    Add Video
                  </button>
                </AddImage>
              </div>
            </div>
          </Baner>
        </div>
      </div>
      )}
      
    </>
  );
};

export default VideoComponent;

const Baner = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  /* .col-3 {
    box-shadow: 0 1px 2px rgb(0 0 0 / 5%), 0 0 0 1px rgb(63 63 68 / 10%);
    background-color: #ffffff;
  } */
  /* .card{
    box-shadow: 0;
  } */
`;

const I = styled.i`
  display: none;
  width: 100%;
  height: 100%;
  i {
    position: absolute;
    top: 31%;
    font-size: 50px;
    left: 44%;
    width: fit-content;
  }
`;
const Image = styled.div`
  position: relative;
  video {
    /* width: 100%; */
    width: 320px;
    /* height: 240px; */
    /* object-fit: contain; */
  }
  &:hover ${I} {
    display: block;
  }
`;

const AddImage = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  input {
    margin: 10px auto;
    width: 70%;
  }
  button {
    background: #567ac8;
    border: none;
    color: white;
  }
`;
