import React, { useEffect } from "react";
import Alert from "@mui/material/Alert";
import Stack from "@mui/material/Stack";
import useAdminContexts from "../../Context/AdminContext";

const AlertComponent = () => {
  const { alert1, setAlert1 } = useAdminContexts();
  useEffect(() => {
    if (alert1) {
      console.log("alert.>>>>>", alert1);
      setTimeout(() => {
        setAlert1("");
      }, 2500);
    }
  }, [alert1]);
  console.log("Enter Alert", alert1);
  return (
    <>
      {alert1 ? (
        <div className="pos-fixed w-100 z-1010">
          <Stack sx={{ width: "100%" }} spacing={2}>
            <Alert severity={alert1.severity}>{alert1.message}</Alert>
            {/* <Alert severity="warning">
              This is a warning alert  check it out!
            </Alert>
            <Alert severity="info">This is an info alert  check it out!</Alert>
            <Alert severity="success">
              This is a success alert  check it out!
            </Alert> */}
          </Stack>
        </div>
      ) : (
        <></>
      )}
    </>
  );
};

export default AlertComponent;
